
.infoshow {
  width: 100%;
  height: 100%;
  box-sizing: border-box;

  .row-bg {
    width: 100%;
    height: 100%;
  }
  .user {
    text-align: center;
    position: relative;
    top: 30%;

    img {
      width: 150px;
      border-radius: 50%;
    }
  }
}

.userinfo {
  height: 100%;
  width: 100%;
  padding: 12px;
  border-left: 1px solid #eee;
  //   background-color: #eee;

  .user-item {
    position: relative;
    top: 35%;
    padding: 5px;
    font-size: 28px;
    color: #333;
  }

  i,
  span {
    margin-right: 10px;
    line-height: 20px;
    font-size: 20px;
    font-weight: bold;
  }
}
